import '@/style/index/index.scss';
import React from 'react';
import Carousel from 'antd/es/carousel';
import 'antd/es/carousel/style/css';
import music from "@/assets/img/666.mp3";
class Main extends React.Component{
    constructor(props){
		super(props);
		this.state={
			imglist:[
				{
					
				},{
					
				},{
					
				},{
					
				},{
					
				},{
					
				},{
					
				},
			],
			html:'<br/>送给高冷的陈思小仙女:<br/>生日快乐!<br/>希望你在未来的日子里，<br/>每一天都被偏爱，<br/>每一天都能睡到自然醒，<br/>每一天都能吃到爱吃的水果，<br/>日子甜甜的，<br/>像清晨的柠檬水，<br/>像初冬的太阳，<br/>像梦里的大海，<br/>像第一次遇见你。<br/>2020，Happy!!!_<br/>',
		}
	}
    render(){
        return(
            <div className='content' style={{backgroundImage:`url(${require('@/assets/img/59.png')})`}}>
                
				<audio autoPlay src={music} loop="loop">
                    您的浏览器不支持 audio 标签
                </audio>
				<div className='content-word'>
					<img className='content-word-img' src={require('@/assets/img/corner_title_5.png')} alt=""/>
					<p dangerouslySetInnerHTML={{ __html: this.state.html}}></p>
				</div>
				{/* <div className='content-english'>
					
				</div> */}
				<div className='content-happy'>
					<img className='content-happy-english' src={require('@/assets/img/corner_1.jpg')} alt=""/>
					<img className='content-happy-ball' src={require('@/assets/img/balloons.png')} alt=""/>
					<img className='content-happy-floow' src={require('@/assets/img/pink_flower.png')} alt=""/>
					<img className='content-happy-si' src={require('@/assets/img/20.gif')} alt=""/>
				</div>
				<img className='content-like' src={require('@/assets/img/like.jpg')} alt=""/>
				<div className='content-imgs'  style={{backgroundImage:`url(${require('@/assets/img/imgbg.png')})`}}>
					<Carousel dots={false} autoplay className='car' effect="fade">
						{this.state.imglist.map((item,index)=>{
							return (
								<div key={index}>
									<img className='bg' src={require('@/assets/img/'+(index+1)+'.jpg')} alt=""/>
								</div>
							)
						})}
  					</Carousel>
               	</div>
            </div>
        )
    }
};
